import YouTube from "react-youtube";
import io from "socket.io-client";
import { useEffect, useRef, useState } from "react";
import {
  Button,
  Comment,
  Divider,
  Form,
  Grid,
  GridColumn,
  Header,
  Label,
  Modal,
  Icon,
  Input,
  Checkbox,
  Container,
} from "semantic-ui-react";
import Message from "./Message";
import User from "./User";

// const socket = io.connect("http://localhost:4000");
const socket = io.connect();

const App = () => {
  const [inputNickname, setInputNickname] = useState("");
  const [inputChat, setInputChat] = useState("");
  const [connectedNickname, setConnectedNickname] = useState("");
  const [chat, setChat] = useState([
    { username: "System", message: "Welcome!", time: "" },
  ]);
  const [userList, setUserList] = useState([]);
  const [syncOn, setSyncOn] = useState(false);
  const player = useRef(null);

  useEffect(() => {
    socket.on("login", ({ username, userList }) => {
      console.log(username);
      if (username) {
        setConnectedNickname(username);
      }
      setUserList(userList);
    });
  }, [userList]);

  useEffect(() => {
    socket.on("user joined", ({ username, userList }) => {
      console.log(username, userList);
      addChatMessage("System", username + " joined", getCurrentTimeString());
      setUserList(userList);
    });
  }, [chat, userList]);

  useEffect(() => {
    socket.on("user left", ({ username, userList }) => {
      console.log(username, userList);
      addChatMessage("System", username + " left", getCurrentTimeString());
      setUserList(userList);
    });
  }, [chat, userList]);

  useEffect(() => {
    socket.on("new message", ({ username, message }) => {
      addChatMessage(username, message, getCurrentTimeString());
    });
  }, [chat]);

  useEffect(() => {
    socket.on("sync", (data) => {
      console.log(data);
      if (!syncOn) {
        syncReceive(data);
      }
    });
  }, [syncOn]);

  useEffect(() => {
    const interval = setInterval(() => {
      if (syncOn) {
        syncSend();
      }
    }, 2000);

    return () => clearInterval(interval);
  }, [syncOn]);

  // useEffect(() => {
  //   return () => {
  //     socket.close();
  //   };
  // }, []);

  socket.on("connect", () => {
    console.log("connect");
  });

  const seekAndPlay = () => {
    player.current.internalPlayer.seekTo(10);
    player.current.internalPlayer.playVideo();
  };

  const getCurrentTimeString = () => {
    const now = new Date();
    return now.getHours() + ":" + now.getMinutes();
  };

  const syncSend = async () => {
    const currentTime = await player.current.internalPlayer.getCurrentTime();
    const statusCode = await player.current.internalPlayer.getPlayerState();

    const data = {
      currentTime,
      isPlaying: statusCode === 1,
    };
    socket.emit("sync", data);
  };

  const syncReceive = async ({ currentTime, isPlaying }) => {
    const mycCurrentTime = await player.current.internalPlayer.getCurrentTime();

    if (Math.abs(mycCurrentTime - currentTime) > 2) {
      player.current.internalPlayer.seekTo(currentTime);
    }

    if (isPlaying) {
      player.current.internalPlayer.playVideo();
    } else {
      player.current.internalPlayer.pauseVideo();
    }
  };

  const addUser = (username) => {
    socket.emit("add user", username);
  };

  const getCurrentTimeFunc = async () => {
    console.log(await player.current.internalPlayer.getCurrentTime());
  };

  const addChatMessage = (username, message, time) => {
    setChat([{ username, message, time }, ...chat].slice(0, 50));
  };

  const sendChatMessage = (message) => {
    if (message) {
      setInputChat("");
      addChatMessage(connectedNickname, message, getCurrentTimeString());
      socket.emit("new message", message);
    }
  };

  const renderChat = () => {
    return chat.map(({ username, message, time }, index) => (
      <Message username={username} message={message} time={time} key={index} />
    ));
  };

  const renderUserList = () => {
    return userList.map((username, index) => (
      <User username={username} key={index} />
    ));
  };

  return (
    <>
      <Container fluid style={{ padding: "20px" }}>
        <Grid columns="equal">
          <Grid.Column verticalAlign="top" textAlign="center" width={12}>
            <Grid.Row>
              <YouTube
                videoId="8zxBfLGX7-Y"
                ref={player}
                opts={{ width: "100%", height: "800px" }}
                // onPause={(e) => console.log(e.target.getPlayerState)}
              />
            </Grid.Row>
            <Divider />

            <Grid.Row>{renderUserList()}</Grid.Row>
            <Divider />

            <Grid.Row>
              <Checkbox
                label="Sync with my current video"
                onChange={() => setSyncOn(!syncOn)}
                checked={syncOn}
              ></Checkbox>
            </Grid.Row>

            <Grid.Row></Grid.Row>
          </Grid.Column>

          <Grid.Column>
            <Comment.Group style={{ width: "100%" }}>
              <Header as="h3" dividing>
                Chats
              </Header>

              <Form>
                <Input
                  action={{
                    icon: "send",
                    content: "Send",
                    onClick: () => sendChatMessage(inputChat.trim()),
                  }}
                  fluid
                  onChange={(e) => setInputChat(e.target.value)}
                  value={inputChat}
                  maxLength={300}
                ></Input>
              </Form>

              {renderChat()}
            </Comment.Group>
          </Grid.Column>
        </Grid>
      </Container>
      <Modal basic open={!connectedNickname}>
        <Header icon>
          <Icon name="chat" />
          Please Enter the Nickname
        </Header>

        <Form>
          <Input
            action={{
              content: "Connect",
              icon: "sign-in",
              onClick: () => {
                if (inputNickname) {
                  addUser(inputNickname);
                }
              },
            }}
            required
            fluid
            maxLength={20}
            onChange={(e) => setInputNickname(e.target.value)}
            value={inputNickname}
          ></Input>
        </Form>
      </Modal>
    </>
  );
};

export default App;
